import Link from 'next/link'
import home from '../icon/home.svg'


const Header = () => {
    return (
        <dir class = "container-fluid" id="bar" >
            <div class="container-fluid">
                <div  class="row" >
                    <div class="col-lg-2" >
                            <Link href="/">
                                <img  src={home} alt="Elderly" width="50px" position="left" /></Link>                   
                    </div>
                    <nav class="navbar navbar-expand-lg navbar-light bg-light">
                    <div class="col-lg-8">
                        <div class="collapse navbar-collapse" id="navbarNavAltMarkup" >
                            <div class="navbar-nav">
                                <a class="nav-item nav-link active" ><h5  id = "frontbar">หน่วยงาน</h5></a>
                                <a class="nav-item nav-link" ><h5  id = "frontbar" >เรื่องน่ารู้</h5></a>
                                <a class="nav-link active" ><h5  id = "frontbar" >ติดต่อเรา</h5></a>
                            </div>
                        </div>
                    </div></nav>

                    <div class="col-lg-2">
                        <div>
                            <h5 id = "frontbarL">สายด่วน โทร. 1300</h5>
                        </div>
                    </div>
                </div>

            </div>

        </dir>
    )
}
export default Header

